import React from 'react';
import { Card, Col, Row, Stack } from 'react-bootstrap';
import './Cards.scss';
import imgSrc from 'assets/icons/CardArrowVector.svg';

interface Props {
  heading: string
  value: number
  footer1Heading: string
  footer1Value: number
  footer2Heading: string
  footer2Value: number
  footer3Heading?: string
  footer3Value?: number,
  cardHeaderClickHandler?: () => void
}
const Cards: React.FC<Props> = ({ heading, value, footer1Heading, footer1Value, footer2Heading, footer2Value, footer3Heading, footer3Value, cardHeaderClickHandler }: Props) => {
  return (
    <div>
      <Row>
        <Col>
          <Card className='card-template'>
            <Card.Header onClick={cardHeaderClickHandler} className='card-template-header cursor-pointer'>
              {heading}
              <img alt='search icon' className='card-template-header-left-icon' src={imgSrc} />
            </Card.Header>
            <Card.Body className='card-template-body'>
              <Stack direction='horizontal' className='card-template-stack'>
                <Card.Title className='card-template-title'>{value}</Card.Title>
              </Stack>
            </Card.Body>
            <Card.Footer className='card-tempalte-footer'>
              <Row>
                <Col xs={4} className='footer-item'>
                  <p>{footer1Heading}</p>
                  <p>{footer1Value}</p>
                </Col>
                <Col xs={4} className='footer-item'>
                  <p>{footer2Heading}</p>
                  <p>{footer2Value}</p>
                </Col>
                {
                  footer3Heading ? (
                    <Col className='footer-item'>
                      <p>{footer3Heading}</p>
                      <p>{footer3Value}</p>
                    </Col>
                  )
                    : null
                }

              </Row>
            </Card.Footer>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Cards;
