import React, { useEffect } from 'react';
import DashboardContainer from 'shared/components/DashboardContainer-component/DashboardContainer';
import Cards from 'shared/components/Card-component/Cards';
import ValidationService from 'services/ValidationService/validation.service';
import './ValidationHome.scss';
import Loader from 'shared/components/Loader';
import { useNavigate } from 'react-router';
import { AppRoutes } from 'routes/routeConstants/appRoutes';
import { Col, Row } from 'react-bootstrap';

const ValidationHome: React.FC = () => {

  const { fetchValidationDashboard, loading, validationDashboardDetails } = ValidationService()

  const navigate = useNavigate();
  useEffect(() => {
    fetchValidationDashboard()
  }, [])

  const redirectToValidationPage = () => {
    navigate(AppRoutes.VALIDATION_RECORDS)
  }

  return (
    <DashboardContainer>
      <div className='page-content'>
        <Row>
          <Col xs={12}>

            <h1 className='page-title'> Checks</h1>
            <p className='page-sub-title'>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit ut aliquam, purus sit amet luctus venenatis, lectus magna fringilla urna, porttitor rhoncus dolor purus non enim praesent elementum facilisis leo, vel fringilla est ullamcorper eget nulla facilisi etiam dignissim diam.
            </p>
          </Col>
        </Row>
        {
          loading
            ? <Loader />
            : <Row>
              <Col xs={4}>
                <Cards
                  heading='Validations'
                  value={validationDashboardDetails?.totalCount ?? 0}
                  footer1Heading='Excel'
                  footer1Value={validationDashboardDetails?.excelCount ?? 0}
                  footer2Heading='Csv'
                  footer2Value={validationDashboardDetails?.csvCount ?? 0}
                  footer3Heading='Word'
                  footer3Value={validationDashboardDetails?.docCount ?? 0}
                  cardHeaderClickHandler={redirectToValidationPage}
                />
              </Col>
              <Col xs={4}>
                <Cards heading='Audit' value={62} footer1Heading='Recent' footer1Value={2} footer2Heading='Old' footer2Value={60} />
              </Col>
              <Col xs={4}>
                <Cards heading='Volatile Engine' value={29} footer1Heading='Files' footer1Value={10} footer2Heading='Videos' footer2Value={60} />
              </Col>
              <Col xs={4}>
                <Cards heading='Source Agnostic Ingestion' value={29} footer1Heading='Files' footer1Value={10} footer2Heading='Videos' footer2Value={19} />
              </Col>
              <Col xs={4}>
                <Cards heading='Data Purging' value={29} footer1Heading='Files' footer1Value={10} footer2Heading='Videos' footer2Value={19} />
              </Col>
            </Row>
        }
      </div>
    </DashboardContainer>
  );
};

export default ValidationHome;
