
import {serializable, alias, object, list, primitive} from 'serializr';

export class SmartMappingList { 

	@serializable(alias('id', primitive()))
	id?: number;

	@serializable(alias('data_provider', primitive()))
	dataProvider?: string;

	@serializable(alias('category', primitive()))
	category?: string;

	@serializable(alias('file_name', primitive()))
	fileName?: string;

	@serializable(alias('country', primitive()))
	country?: string;

	@serializable(alias('total', primitive()))
	total?: string;

	@serializable(alias('success_status', primitive()))
	successStatus?: number;

	@serializable(alias('file_extension', primitive()))
	fileExtension?: object;

	@serializable(alias('mapped_record_count', primitive()))
	mappedRecordCount?: number;

	@serializable(alias('unmapped_record_count', primitive()))
	unmappedRecordCount?: number;

	@serializable(alias('confidence_level_less_than_70', primitive()))
	confidenceLevelLessThan70?: string;

	@serializable(alias('confidence_level_50_70', primitive()))
	confidenceLevel5070?: string;

	@serializable(alias('confidence_level_less_than_50', primitive()))
	confidenceLevelLessThan50?: string;

	@serializable(alias('createdAt', primitive()))
	createdAt?: string;

	@serializable(alias('updatedAt', primitive()))
	updatedAt?: string;

}