
import { serializable, alias, object, list, primitive } from 'serializr';

export class SmartMappingMappedResult {

	@serializable(alias('id', primitive()))
	id?: number;

	@serializable(alias('TAG', primitive()))
	TAG?: string;

	@serializable(alias('UNILEVER_DESC', primitive()))
	UNILEVERDESC?: string;

	@serializable(alias('VENDOR_DESC', primitive()))
	VENDORDESC?: string;

	@serializable(alias('SKUCODE', primitive()))
	SKUCODE?: string;

	@serializable(alias('CATEGORY', primitive()))
	CATEGORY?: string;

	@serializable(alias('DIVISION', primitive()))
	DIVISION?: string;

	@serializable(alias('CATEGORY_GROUP', primitive()))
	CATEGORYGROUP?: string;

	@serializable(alias('SEGMENT', primitive()))
	SEGMENT?: string;

	@serializable(alias('PRODUCT_FROM_MEDIUM_NAME', primitive()))
	PRODUCTFROMMEDIUMNAME?: string;

	@serializable(alias('MAPPED_STATUS', primitive()))
	MAPPEDSTATUS?: boolean;

	@serializable(alias('createdAt', primitive()))
	createdAt?: string;

	@serializable(alias('updatedAt', primitive()))
	updatedAt?: string;

	@serializable(alias('smart_mapping_list_id', primitive()))
	smartMappingListId?: number;

	@serializable(primitive())
	isSelected?: boolean = false;

	@serializable(primitive())
	isChecked?: boolean = false;

}