import React, { useEffect } from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import CustomButton from 'shared/components/Button-component/Button'
import SearchInputField from 'shared/components/Search-input-field/search-input-field';
import UnmappedCup from 'assets/icons/unmapped-cup.svg';
import ArrowImg from 'assets/icons/CardArrowVector.svg';
import Table from 'shared/components/Table-component/Table-component';
import ValidationService from 'services/ValidationService/validation.service'
import { useParams } from 'react-router';
import './Unmapped.scss';

const Unmapped = () => {
  const [showTable, setshowTable] = React.useState<number | undefined>();

  const { fetchUnMappedData, mappedData, toggleMappedCheckStatus, toggleUnMappedCheckStatus, submitUnMappedResult } = ValidationService()
  const { id } = useParams();

  const handleShowTable = (index: string | undefined, id: string | undefined) => {
    if (!index) return
    let selectedIndex = parseInt(index)
    toggleMappedCheckStatus(id)
    setshowTable(selectedIndex);
  }
  const itemsArr = [
    { id: 1, value: 'Hindustan Unilever Limited AXE AXE Dark Temptataipon' },
    { id: 2, value: 'Hindustan Unilever Limited AXE AXE Dark Temptataipon' },
    { id: 3, value: 'Hindustan Unilever Limited AXE AXE Dark Temptataipon' },
    { id: 4, value: 'Hindustan Unilever Limited AXE AXE Dark Temptataipon' },
    { id: 5, value: 'Hindustan Unilever Limited AXE AXE Dark Temptataipon' },
    { id: 6, value: 'Hindustan Unilever Limited AXE AXE Dark Temptataipon' },
    { id: 7, value: 'Hindustan Unilever Limited AXE AXE Dark Temptataipon' },
    { id: 8, value: 'Hindustan Unilever Limited AXE AXE Dark Temptataipon' },
    { id: 9, value: 'Hindustan Unilever Limited AXE AXE Dark Temptataipon' }
  ]
  const columns: any = [
    {
      id: 'checkboxes',
      accessor: 'isChecked',
      Cell: ({ value, row }: any) => {
        return (
          <input
            type="radio"
            className="radio"
            checked={value}
            onChange={() => toggleUnMappedCheckStatus(row?.original?.id)}
          // checked={this.state.selected[original.firstName] === true}
          // onChange={() => this.toggleRow(original.firstName)}
          />
        );
      }
    },
    {
      Header: 'Product Name',
      accessor: 'UNILEVERDESC',
      minWidth: 300
    },
    {
      Header: 'Division',
      accessor: 'DIVISION',
      minWidth: 200
    },
    {
      Header: 'Category',
      accessor: 'category',
      columns: [
        {
          Header: 'Group',
          accessor: 'CATEGORYGROUP',
          minWidth: 200,
        },
        {
          Header: 'Name',
          accessor: 'name',
          minWidth: 200,
        }
      ]
    },
    {
      Header: 'Segment',
      accessor: 'SEGMENT',
      minWidth: 150
    },
    {
      Header: 'Product from Medium Name',
      accessor: 'PRODUCTFROMMEDIUMNAME',
      minWidth: 150
    },
    {
      Header: 'Brand from Name',
      accessor: 'brandFromName',
      minWidth: 150
    }
  ];

  useEffect(() => {
    id && fetchUnMappedData(id)
  }, [])

  if (!id) return null

  console.log("Mapped data is ", mappedData)

  const isSubmitButtonEnabled = mappedData.some((item) => item.isChecked === true)

  return (
    <Container fluid className='container-unmapped'>
      <Row className='header-row'>
        <Col sm={3} className='unmapped-items-header'>
          <div className='count'>{mappedData.length}</div>
          <div className='heading-container'>
            <h2>Unmapped Items</h2>
            <p>From data providers file</p>
          </div>
        </Col>
        <Col sm={9} className='nearest-match-header'>
          <Col sm={5} className='heading-container'>
            <div className='count'>{showTable !== undefined ? 1 : 0}</div>
            <p>Nearest Match</p>
          </Col>
          <SearchInputField
            // onChange={searchHandler}
            placeholder='Search...' width='354px' height='48px' />
        </Col>
      </Row>
      <Row>
        <Col sm={3} className='list-container'>
          {mappedData.map((item, index) => {
            return (
              <button key={item.id} className='list-item-container' type="button" onClick={() => handleShowTable(index.toString(), item?.id?.toString())}
                style={{
                  backgroundColor: (showTable === index|| item.isChecked) ? 'rgba(0, 177, 144, 0.05)' : 'white',
                  border: showTable === index ? '1px solid #1F36C7' : '1px solid #D9D9D9'
                }}>{item.VENDORDESC}
                {showTable === index && <img src={ArrowImg} alt='arrow' className='arrow-logo-btn' />}
              </button>
            );
          })}
        </Col>
        <Col sm={9} className='span-area'>
          {
            showTable !== undefined ? (
              <div className='table-container-unmapped'>
                {
                  <Table columns={columns} data={[mappedData[showTable]]} />
                }
              </div>
            ) : <>
              <img src={UnmappedCup} alt='cup' />
              <p>Click one of the unmapped Item to view the nearest match. </p>
            </>
          }
        </Col>
      </Row>
      <Row>
        <Col sm={9} className='footer-unmapped'>
          <CustomButton secondary>Cancel</CustomButton>
          <CustomButton disabled={!isSubmitButtonEnabled} onClick={()=>submitUnMappedResult(id)}>Submit</CustomButton>
        </Col>
      </Row>
    </Container>
  )
}

export default Unmapped