import React, { useEffect } from 'react'
import { Col, Row, Container } from 'react-bootstrap'
import CustomButton from 'shared/components/Button-component/Button'
import SearchInputField from 'shared/components/Search-input-field/search-input-field'
import './Mapped.scss';
import Table from 'shared/components/Table-component/Table-component';
import EDPagination from 'shared/components/Pagination';
import ValidationService from 'services/ValidationService/validation.service';
import { useParams } from 'react-router';
import { searchUtil } from 'shared/utils/searchUtil';
import Loader from 'shared/components/Loader';

const Mapped = () => {

  const columns: any = [
    {
      id: 'checkboxes',
      accessor: 'isSelected',
      Cell: ({ value, row }: any) => {
        return (
          <input
            type="checkbox"
            className="checkbox"
            checked={value}
            onChange={() => toggleMappedCheckStatus(row?.original?.id)}
          />
        );
      },
      Header: () => {
        return (
          <input
            type="checkbox"
            className="checkbox header-checkbox"
            checked={toggleAllStatus}
            onChange={() => toggleAllCheckbox()}
          />
        );
      },

    },
    {
      Header: 'Product Name',
      accessor: 'ProductName',
      minWidth: 100,
      columns: [
        {
          Header: 'Internal (Unilever)',
          accessor: 'UNILEVERDESC',
          minWidth: 200
        },
        {
          Header: 'External (Nielsen)',
          accessor: 'VENDORDESC',
          minWidth: 200
        },
      ]
    },
    {
      Header: 'Division',
      accessor: 'DIVISION',
      minWidth: 200
    },
    {
      Header: 'Category',
      accessor: 'category',
      columns: [
        {
          Header: 'Group',
          accessor: 'CATEGORYGROUP',
          minWidth: 200,
        },
        {
          Header: 'Name',
          accessor: '',
          minWidth: 200,
        }
      ]
    },
    {
      Header: 'Segment',
      accessor: 'SEGMENT',
      minWidth: 150
    },
    {
      Header: 'Product from Medium Name',
      accessor: 'PRODUCTFROMMEDIUMNAME',
      minWidth: 150
    },
    {
      Header: 'Brand from Name',
      accessor: 'brandFromName',
      minWidth: 150
    }
  ];

  const data = [
    {
      internal: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      external: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      division: 'HC & BPC & H&W',
      group: 'Beauty & Personal care',
      name: 'Deodorant & Fragrance',
      segment: 'Deo Aerosois Non AP',
      productFromMediumName: 'Deo Aerosois Non AP',
      brandFromName: 'Axe Aero Alcoholic Prem'
    },
    {
      internal: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      external: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      division: 'HC & BPC & H&W',
      group: 'Beauty & Personal care',
      name: 'Deodorant & Fragrance',
      segment: 'Deo Aerosois Non AP',
      productFromMediumName: 'Deo Aerosois Non AP',
      brandFromName: 'Axe Aero Alcoholic Prem'
    },
    {
      internal: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      external: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      division: 'HC & BPC & H&W',
      group: 'Beauty & Personal care',
      name: 'Deodorant & Fragrance',
      segment: 'Deo Aerosois Non AP',
      productFromMediumName: 'Deo Aerosois Non AP',
      brandFromName: 'Axe Aero Alcoholic Prem'
    },
    {
      internal: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      external: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      division: 'HC & BPC & H&W',
      group: 'Beauty & Personal care',
      name: 'Deodorant & Fragrance',
      segment: 'Deo Aerosois Non AP',
      productFromMediumName: 'Deo Aerosois Non AP',
      brandFromName: 'Axe Aero Alcoholic Prem'
    },
    {
      internal: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      external: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      division: 'HC & BPC & H&W',
      group: 'Beauty & Personal care',
      name: 'Deodorant & Fragrance',
      segment: 'Deo Aerosois Non AP',
      productFromMediumName: 'Deo Aerosois Non AP',
      brandFromName: 'Axe Aero Alcoholic Prem'
    },
    {
      internal: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      external: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      division: 'HC & BPC & H&W',
      group: 'Beauty & Personal care',
      name: 'Deodorant & Fragrance',
      segment: 'Deo Aerosois Non AP',
      productFromMediumName: 'Deo Aerosois Non AP',
      brandFromName: 'Axe Aero Alcoholic Prem'
    },
    {
      internal: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      external: 'Axe Deo Aer Hypnotic 12X113GR/ 160 ML',
      division: 'HC & BPC & H&W',
      group: 'Beauty & Personal care',
      name: 'Deodorant & Fragrance',
      segment: 'Deo Aerosois Non AP',
      productFromMediumName: 'Deo Aerosois Non AP',
      brandFromName: 'Axe Aero Alcoholic Prem'
    }
  ]

  const {
    fetchMappedData, mappedData, loading, pageSize, toggleMappedCheckStatus, toggleAllCheckbox, toggleAllStatus, pagination
  } = ValidationService()

  const { id } = useParams();

  useEffect(() => {
    id && fetchMappedData(id)
  }, [])

  if (!id) return null

  const handlePagination = (pageNumber: number) => {
    const limit = pageSize
    const offset = (pageNumber - 1) * limit
    fetchMappedData(id, {
      limit,
      offset
    })
  }

  const isRaiseIssueButtonEnable = mappedData.some((item) => item.isSelected === true)

  return (
    <Container fluid className='container-mapped'>
      <Row>
        <Col sm={4}><CustomButton disabled={!isRaiseIssueButtonEnable} >Raise Issue</CustomButton></Col>
        <Col sm={8} className='search-container'>
          <SearchInputField
            onChange={searchUtil((e: any) => fetchMappedData(id, { search: e.target.value }))}
            placeholder='Search...' width='354px' height='48px' />
        </Col>
      </Row>
      <Row sm={12}>
        {
          loading
            ? <Loader />
            : <>
              <div className='table-container'>
                <Table columns={columns} data={mappedData} />
              </div>
              {
                pagination && (
                  <EDPagination
                    page={pagination?.page ?? 0}
                    pageSize={pagination?.pageSize ?? 0}
                    totalCount={pagination?.totalCount ?? 0}
                    onPageItemClick={handlePagination}
                  />
                )
              }

            </>
        }

      </Row>
    </Container>
  )
}


export default Mapped