import React from 'react';
import './Sidebar.scss';
import UniliverLogo from 'assets/icons/uniliver-logo.svg';
import EdithLogo from 'assets/icons/edith-logo.svg';
import HomeWhite from 'assets/icons/home-white.svg';
import Home from 'assets/icons/home.svg';
import TasksWhite from 'assets/icons/tasks-white.svg';
import Reports from 'assets/icons/reports.svg';
import imgSrc from 'assets/icons/menu-stack.svg';
import RecordsWhite from 'assets/icons/records-white.svg';
import ChecksWhite from 'assets/icons/checks-white.svg';
import NotificationsWhite from 'assets/icons/notification-white.svg';
// import clockImg from 'assets/icons/clock.svg';
const Sidebar: React.FC = () => {
  const menuItems = [
    {
      name: 'Home',
      icon: HomeWhite,
      active: false,
      'icon-active': Home
    },
    {
      name: 'Tasks',
      icon: TasksWhite,
      active: false,
      'icon-active': 'assets/icons/tasks.svg'
    },
    {
      name: 'Reports',
      icon: Reports,
      active: false,
      'icon-active': 'assets/icons/reports.svg'
    },
    {
      name: 'Checks',
      icon: ChecksWhite,
      active: true,
      'icon-active': 'assets/icons/checks.svg'
    },
    {
      name: 'Records',
      icon: RecordsWhite,
      active: false,
      'icon-active': 'assets/icons/records.svg'
    },
    {
      name: 'Notifications',
      icon: NotificationsWhite,
      active: false,
      'icon-active': 'assets/icons/notifications.svg'
    }
  ];
  
  return (
    // sidebar container
    <div className='sidebar-container'>
      {/* header */}
      <div className='sidebar-header'>
        <img src={imgSrc} alt='menu' />
        <img src={UniliverLogo} alt='menu' />
        <img src={EdithLogo} alt='menu' />
      </div>
      {/* menu items */}
      <div className='sidebar-menu-container'>
        {/* Each item - menu */}
        {menuItems.map((item) => {
          if (item.active) {
            return (
              <>
              <div className='active-container active'>
              <div className='sidebar-menu-item active'>
                <div className='icon'>
                  <img src={item.icon} alt='icon' />
                </div>
                <div className='text'>
                  <p>{item.name}</p>
                </div>
                <div className='marker'></div>
              </div>
              </div>
              </>
            )
          } else {
            return (
              <div className='active-container '>
              <div className='sidebar-menu-item'>
                <div className='icon'>
                  <img src={item.icon} alt='icon' />
                </div>
                <div className='text'>
                  <p>{item.name}</p>
                </div>
              </div>
              </div>
            );
          }
        })}
      </div>
        {/* <div className='currentlogin-container'>
        <div className='rowDiv'>
        <img src={clockImg} alt='clock'/>
        <p>Current Login</p>
        <p>02.59 PM, 06 Jan 23</p>
        </div>
        </div> */}
      </div>
  );
};

export default Sidebar;
