import React from 'react'
// import { Tabs, Tab } from 'react-bootstrap'
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import InputText from '../InputText-component/inputText'
import './Tabs.scss'
interface Props {
  component1: React.ReactNode
  component2: React.ReactNode

}
const TabComponent: React.FC<Props> = ({component1, component2}:Props) => {
  return (
    <div>
      <Tabs defaultActiveKey='mapped' id='uncontrolled-tab-example' className='mb-3 tab-component'>
        <Tab className='tab-component-mapped' eventKey='mapped' title='Mapped'>{component1}</Tab>
        <Tab className='tab-component-unmapped' eventKey='unmapped' title='Unmapped'>
          {component2}
        </Tab>
      </Tabs>
    </div>
  )
}

export default TabComponent;
