import React, { FC } from 'react'
import { Col, Pagination, Row } from 'react-bootstrap'
import DropdownComponent from '../dropdown-component'
import PageLink from './PageLink'
import "./pagination.scss"

interface EDPaginationProps {
    page: number,
    totalCount: number,
    pageSize: number,
    onPageItemClick: (pageNumber: number) => void
}

const EDPagination: FC<EDPaginationProps> = (props) => {

    const { page, totalCount, pageSize, onPageItemClick } = props

    let active = page;
    let totalPages = Math.ceil(totalCount / pageSize)
    let items = [];

    const isFirstPage = page === 1
    const isLastPage = page === totalPages

    const noop = () => { }

    for (let number = 1; number <= totalPages; number++) {
        items.push(
            <Pagination.Item
                onClick={(number === active) ? noop : () => onPageItemClick(number)}
                key={number}
                active={number === active
                }
            >
                {number}
            </Pagination.Item >
        );
    }

    const recordsDropdown = [
        {
            label: "10",
            value: "10"
        },
        {
            label: "20",
            value: "20"
        },
        {
            label: "30",
            value: "30"
        },
        {
            label: "40",
            value: "40"
        },
    ]

    return (
        <div className="pagination w-100">
            <Row className='w-100 align-items-center'>
                <Col xs={4}>
                    <p className='records-label'>
                        {totalCount} records found
                    </p>
                </Col>
                <Col xs={3}>
                    <p className='records-label'>
                        Cards per page
                        <DropdownComponent className='ml-3' width='50px' height='40px' options={recordsDropdown} />
                    </p>
                </Col>
                <Col xs={5} className="pr-0">
                    <Pagination className='float-right'>
                        <PageLink
                            onPageItemClick={isFirstPage ? noop : () => onPageItemClick(1)}
                            disabled={isFirstPage}
                            type='prev'
                            redirectPageNumber={1}
                        />
                        {items}
                        <PageLink
                            onPageItemClick={isLastPage ? noop : () => onPageItemClick(totalPages)} disabled={isLastPage} type='next'
                            redirectPageNumber={totalPages}
                        />
                    </Pagination>
                </Col>
            </Row>
        </div>
    )
}

export default EDPagination