import React from 'react'
import { Container, Navbar, Row, Col } from 'react-bootstrap'
import './Nav-component.scss'
import Network from 'assets/icons/network.svg'
import NotificationBell from 'assets/icons/notification-bell.svg'
import Profile from 'assets/icons/profile-nav.svg'
import SearchInputField from '../Search-input-field/search-input-field'
import Dropdown from 'shared/components/dropdown-component';

const NavComponent: React.FC = () => {

  const languageDropdownOptions = [
    {
      value: 'english',
      label: 'English'
    },
    {
      value: 'arabic',
      label: 'Arabic'
    }
  ]

  return (
    <Navbar className='navbar-template w-100 d-block' expand='lg'>
      <Container fluid>
        <Row className='w-100'>
          <Col xs={4}>
            <div className='navbar-search'>
              <SearchInputField placeholder='Type here to search file, report...' height='40px' width='150px' />
            </div>
          </Col>
          <Col xs={6} className='text-right'>
            <div className='d-inline-block mr-3 zoom-controls-container'>
              <span className='mr-3'>
                A-
              </span>
              <span className='mr-3 reset-zoom'>
                A
              </span>
              <span className='mr-3'>
                A+
              </span>
            </div>
            <Dropdown className='mr-4' width='100px' height='40px' options={languageDropdownOptions} />
            <div className='screen-reader-access'>
              Screen Reader Access
            </div>
          </Col>
          <Col xs={2}>
            <div className='d-flex justify-content-between'>
              <img src={Network} alt='networking' />
              <img src={NotificationBell} alt='notifications' />
              <img src={Profile} alt='profile' />
            </div>
          </Col>
        </Row>
      </Container>
    </Navbar>
  );
};

export default NavComponent;
