import React from 'react';
import './Dropdown.scss';

interface optionType {
  value?: string | undefined
  label?: string | undefined
}
interface Props {
  options: optionType[]
  width: string
  height: string
  onChange?: () => void,
  placeholder?: string,
  className?: string
}
const SelectInput: React.FC<Props> = ({ options, width, height, onChange, placeholder, className }: Props) => {

  let _options = options
  if (placeholder) {
    _options = [
      {
        label: placeholder,
        value: ""
      },
      ...options
    ]
  }

  const style = {
    width: width ?? '200px',
    height: height ?? '40px',
    fontSize: '12px',
    color: '#576571'
  };
  return (
    <select style={style} onChange={onChange} className={`dropdown-styling ${className} `} >
      {_options.map((option: optionType) => (
        <option key={option.value} value={option.label} style={style}>
          {option.label}
        </option>
      ))}
    </select>
  );
};
export default SelectInput;
