import React from 'react'
import { Routes, Route, BrowserRouter } from 'react-router-dom'
import type { RouterProps } from '../shared/types/route.type'
import { AppRoutes } from './routeConstants/appRoutes'
import DesignLibrary from 'views/DesignLibrary'
import ValidationHome from 'views/ValidationFlow/ValidationHome'
import ValidationDashboard from 'views/ValidationFlow/ValidationDashboard'
import ValidationRecordDetails from 'views/ValidationFlow/ValidationRecordDetails'


const AppRouter: React.FC = () => {
  const routes: RouterProps[] = [
    {
      path: AppRoutes.ROOT,
      component: <ValidationHome />
    },
    {
      path: AppRoutes.VALIDATION_HOME,
      component: <ValidationHome />
    },
    {
      path: AppRoutes.VALIDATION_RECORDS,
      component: <ValidationDashboard />,
    },
    {
      path: AppRoutes.VALIDATION_RECORD_DETAILS,
      component: <ValidationRecordDetails />
    }
  ]

  if (process.env.REACT_APP_UNDER_DEVELOPMENT === 'true') {
    routes.push({
      path: AppRoutes.ROOT,
      component: <DesignLibrary />
    })
  }

  return (
  <div>
      <BrowserRouter>
        <Routes>
          {routes?.map((route, index) => {
            return <Route key={index} path={route.path} element={route?.component} />
          })}
        </Routes>
      </BrowserRouter>
    </div>
  )
}

export default AppRouter
