import React, { useEffect } from 'react'
import DashboardContainer from 'shared/components/DashboardContainer-component/DashboardContainer'
import Table from 'shared/components/Table-component/Table-component'
import SearchInputField from 'shared/components/Search-input-field/search-input-field';
import Dropdown from 'shared/components/dropdown-component';
import './ValidationDashboard.scss';
import ValidationService from 'services/ValidationService/validation.service';
import Loader from 'shared/components/Loader';
import Arrow from 'assets/icons/CardArrowVector.svg'
import moment from 'moment';
import { searchUtil } from 'shared/utils/searchUtil';
import { Breadcrumb } from 'react-bootstrap';
import 'shared/components/Breadcrumb-component/Breadcrumb-component.scss';
import leftIcon from 'assets/icons/left-icon.svg';
import { Router, useNavigate, useRoutes } from 'react-router';
import { AppRoutes } from 'routes/routeConstants/appRoutes';
import EDPagination from 'shared/components/Pagination';
interface rowClickProps {
  rowInfo: any
}
const ValidationDashboard: React.FC = () => {
  const navigate = useNavigate();
  const handleUnmapped = (cell: any) => {
    navigate(`/validation-record-details/${cell?.row?.original.id}`, { state: 'unmapped' })
  }
  const handleMapped = (cell: any) => {
    navigate(`/validation-record-details/${cell?.row?.original.id}`, { state: 'mapped' })
  }
  const handleFile = () => {
    navigate('/');
  }
  const dropdownOptions = [
    {
      value: 'this week',
      label: 'Duration:This Week'
    }
  ]
  const dropdownOptions3 = [
    {
      value: 'this week2',
      label: 'Region: All(22)'
    }
  ]
  const dropdownOptions2 = [
    {
      value: '',
      label: 'Select Category'
    },
    {
      value: 'Beauty & Personal Care',
      label: 'Beauty & Personal Care'
    }
  ]

  const columns: any = [
    {
      Header: 'Data Provider',
      accessor: 'dataProvider',
      minWidth: 100,
    },
    {
      Header: 'Category',
      accessor: 'category',
      minWidth: 200,
    },
    {
      Header: 'Filename',
      accessor: 'fileName',
      minWidth: 40,
      Cell: (cell: any) => <div onClick={handleFile} onKeyDown={handleFile} role="link" tabIndex={0} className='cell-hyperlink'>{cell.value}</div>
    },
    {
      Header: 'Loaded On',
      accessor: 'createdAt',
      minWidth: 100,
      Cell: ({ value }: any) => moment(value).format("D/M/YYYY, hh:mm A")
    },
    {
      Header: 'Records',
      className: 'active',
      minWidth: 300,
      columns: [
        {
          Header: 'Total',
          accessor: 'total',
          minWidth: 100,
        },
        {
          Header: 'Mapped',
          accessor: 'mappedRecordCount',
          width: 10,
          Cell: (cell: any) => <div onClick={() => handleMapped(cell)} onKeyDown={() => handleMapped(cell)} role="link" tabIndex={0} className='cell-hyperlink'>{cell.value}</div>
        },
        {
          Header: 'Unmapped',
          accessor: 'unmappedRecordCount',
          width: 10,
          Cell: (cell: any) => <div onClick={() => handleUnmapped(cell)} onKeyDown={() => handleUnmapped(cell)} role="link" tabIndex={0} className='cell-hyperlink'>{cell.value}</div>
        },
        {
          Header: 'Success Status(%)',
          width: 10,
          accessor: 'successStatus',
        }
      ]
    },
    {
      Header: 'Confidence Level of data mapping for records(in %)',
      minWidth: 300,
      columns: [
        {
          Header: '>70',
          accessor: 'confidenceLevelLessThan70',
          minWidth: 80
        },
        {
          Header: '>50 till <70',
          accessor: 'confidenceLevel5070',
          minWidth: 100
        },
        {
          Header: '<50',
          accessor: 'confidenceLevelLessThan50',
          minWidth: 80
        },
        {
          Header: '',
          id: 'icon',
          Cell: () => (<img src={Arrow} alt='alt' />)
        }

      ]
    }
  ];

  const {
    fetchSmartMappingList, loading, smartMappingList, fetchMetaData,
    countryList, providerMeta, pageSize, setPageSize, pagination
  } = ValidationService()
  useEffect(() => {
    fetchMetaData()
    fetchSmartMappingList()
  }, [])

  const redirectToCheckDashboard = () => {
    navigate(AppRoutes.VALIDATION_HOME)
  }

  const onRowClick = () => {
    console.log('clicked', this)
  }

  const handlePagination = (pageNumber: number) => {
    const limit = pageSize
    const offset = (pageNumber - 1) * limit
    fetchSmartMappingList({
      limit,
      offset
    })
  }

  return (
    <DashboardContainer className='validation-dashboard'>
      <div className='dashboard-nav-container w-100'>
        <div className='top-row'>
          <div className='breadcrumb-container'>
            <p className='page-heading'>Validation</p>
            <div>
              <Breadcrumb className='breadcrumb-component'>
                <img className='card-template-header-left-icon' src={leftIcon} alt='left-icon' />
                <Breadcrumb.Item onClick={redirectToCheckDashboard} className='breadcrumb-component-item'>Checks</Breadcrumb.Item>
                <Breadcrumb.Item active className='breadcrumb-component-item'>Validations</Breadcrumb.Item>
              </Breadcrumb>
            </div>
          </div>
          <div className='filter-container'>
            <p>Filter: </p>
            <Dropdown onChange={() => fetchSmartMappingList()} width='158px' height='44px' options={dropdownOptions} />
            <Dropdown onChange={() => fetchSmartMappingList()} width='149px' height='44px' placeholder='Select Country' options={countryList} />
          </div>
        </div>
        <div className='fields-container'>
          <div className='dropdown-container'>
            <Dropdown onChange={() => fetchSmartMappingList()} width='268px' height='44px' placeholder='Select Data Provider' options={providerMeta} />
            <Dropdown onChange={() => fetchSmartMappingList()} width='268px' height='44px' options={dropdownOptions2} />
          </div>
          <div className='right-end-container'>
            <div className='filter-container'>
              <SearchInputField
                onChange={searchUtil((e: any) => fetchSmartMappingList({ search: e.target.value }))}
                placeholder='Search by file name, category...' width='354px' height='48px' />
            </div>
          </div>
        </div>
        {
          loading
            ? <Loader />
            : <>
              <div className='table-container'>
                <Table columns={columns} data={smartMappingList} onRowClick={onRowClick} />
              </div>
              {
                pagination && (
                  <EDPagination
                    page={pagination?.page ?? 0}
                    pageSize={pagination?.pageSize ?? 0}
                    totalCount={pagination?.totalCount ?? 0}
                    onPageItemClick={handlePagination}
                  />
                )
              }
            </>
        }

      </div >
    </DashboardContainer >

  )
}

export default ValidationDashboard
